import * as THREE from 'three';
import Environment from './Environment.js'
import PlaneElement from '../../Global/Elements/PlaneElement.js'
import Resources from '../Utils/Resources.js'

import stageSources from './StageSources.js'

import stageConfig from './StageConfig.js';
import Hotspot from '../../Global/Hotspot.js'

import bottleVertexShader from './shaders/bottle/vertex.glsl'
import bottleFragmentShader from './shaders/bottle/fragment.glsl'
import ModelElement from '../../Global/Elements/ModelElement.js';
import WebExperience from '../WebExperience.js';


export default class Stage
{
    constructor()
    {
        this.experience = new WebExperience()
        this.scene = this.experience.scene

        this.stageResources = new Resources(stageSources)
        this.stageObjects = new THREE.Group()
        
        this.stageResources.on('ready', () =>
        {

            this.stageEnvironment = new Environment()

            this.pack01 = new ModelElement('pack01', this.stageResources.items.pack01)
            this.stageObjects.add(this.pack01.element)
            this.pack02 = new ModelElement('pack02', this.stageResources.items.pack02)
            this.stageObjects.add(this.pack02.element)
            this.pack03 = new ModelElement('pack03', this.stageResources.items.pack03)
            this.stageObjects.add(this.pack03.element)

            this.stageObjects.scale.x = 20
            this.stageObjects.scale.y = 20
            this.stageObjects.scale.z = 20

            this.stageObjects.position.set(15, -10, 15)

        })
    }


    update()
    {

        this.stageObjects.lookAt(this.experience.camera.instance.position)

    }

    hideScene(){
        this.scene.remove(this.stageObjects)
    }
    showScene(){
        this.scene.add(this.stageObjects)

    }

}