  async getMediaStream(constraints) {
    try {
      this.mediaStream =  await navigator.mediaDevices.getUserMedia(constraints);
       
      this.video.srcObject = this.mediaStream;
      this.video.onloadedmetadata = (event) => {
        this.video.play();
      };
    } catch (err)  {    
      console.error(err.message);   
    }
  }

