import * as THREE from 'three';
// import Experience from '../Experience/Experience';
import BaseElement from './Elements/BaseElement';
import { Vector2 } from 'three';

export default class Hotspot extends BaseElement {
  constructor(_name, _degreesX = -131, _degreesY = -15, x = 5, y = 5) {
    super(_name)    
    let degreesY = _degreesX
    let degreesX = _degreesY
    let sizeX = x
    let sizeY = y

    // this.experience = new Experience()
    // this.time = this.experience.time



    this.mesh = new THREE.Mesh(
      new THREE.PlaneGeometry(sizeX, sizeY, 4, 4),
      new THREE.MeshPhongMaterial({
        color: 'red', 
        side: THREE.DoubleSide, 
        transparent: true, 
        opacity: 0, 
        // false para que quede al frente
        depthTest: false, 
        depthWrite: false,
      })
    );
    // this.mesh.rotation.x = -Math.PI/2
    // this.mesh.rotation.z = Math.PI/2
    this.mesh.position.x = -40
    // this.mesh.position.z = -2

    // this.element.rotation.y = Math.PI/2


    this.element.rotation.y = degreesY * (Math.PI/180)
    this.element.rotation.x = degreesX * (Math.PI/180)

    


    this.element.add(this.mesh)

    this.mesh.name = _name


    this.mousePosition = new Vector2()
    this.raycaster = new THREE.Raycaster();

    let target = this

    addEventListener('click',e =>{
      if(!this.experience.active) return
      if(this.active) return


      target.mousePosition.x = (e.clientX / window.innerWidth) * 2 - 1;
      target.mousePosition.y = -(e.clientY / window.innerHeight) * 2 + 1;
    // console.log("X = "+target.mousePosition.x);
    // console.log("Y = "+target.mousePosition.y);
      this.raycaster.setFromCamera(target.mousePosition,this.experience.camera.instance);
      // const intersects = this.raycaster.intersectObjects( this.experience.scene.children );
      const intersects = this.raycaster.intersectObjects( this.experience.clickable );

      // console.log(this.experience.clickable);
      // console.log(this.experience.scene.children);
      
    // for ( let i = 0; i < intersects.length; i ++ ) {


    // console.log(intersects[0].object.name);
    // console.log(this.mesh.name);

    if(intersects.length>0){
      // console.log(intersects[0].object.name);
      if(intersects[0].object.name === this.mesh.name){
        // console.log(intersects[i]);
        // console.log('mesh click');
        // console.log(i);
        // console.log(`clicked ${this.name}`);
        // console.log(`clickedclickedclickedclickedclickedclickedclicked`);
        this.trigger('hotspot_click');

      }
    }
      // console.log(intersects[i]);

      // intersects[ i ].object.material.color.set( 0xff0000 );

    // }
    })

    addEventListener('mousemove', e =>{
      if(!this.experience.active) return

      target.mousePosition.x = (e.clientX / window.innerWidth) * 2 - 1;
      target.mousePosition.y = -(e.clientY / window.innerHeight) * 2 + 1;


    })



    this.time.on('runFrame', () =>
      {
          this.update()
      })
  }


  update(){
    if(!this.experience.active) return
    // console.log('update');
    this.mesh.lookAt(this.experience.camera.instance.position)

    // this.element.rotation.y += 0.001

    // this.raycaster.setFromCamera(this.mousePosition,this.experience.camera.instance);
    // const intersects = this.raycaster.intersectObjects( this.experience.scene.children );

    // for ( let i = 0; i < intersects.length; i ++ ) {

    //   if(intersects[i].object.name === this.mesh.name){
    //     // console.log(intersects[i]);
    //     // console.log('mesh click');
    //     // console.log(i);
    //     // console.log(`over ${this.name}`);
    //     // this.trigger('hotspot_click');

    //   }
    // }

  }

}
